export const translateCategory = category => {
  switch (category) {
    case '':
      return 'Products';
    case '':
      return 'Alcohol';
    case '':
      return 'Entertainment';
    case '':
      return 'Health';
    case '':
      return 'Transport';
    case '  ':
      return 'Housing';
    case '':
      return 'Technique';
    case '  ':
      return 'Communal, communication';
    case '  ':
      return 'Sports, hobbies';
    case '':
      return 'Education';
    case '':
      return 'Other';
    case '/':
      return 'Salary';
    case '. ':
      return 'Add.income';
    default:
      throw new Error(`Unsupported category value - ${category}`);
  }
};
